export const fetchBooks = createAsyncThunk('books/fetch', async () => {
  try {
    const books = await booksShelAPI.fetchBooks(); //     
    console.log(books);
    return books; //   
  } catch (error) {}
});

export const fetchBookInfo = createAsyncThunk('books/fetch', async id => {
  try {
    const books = await booksShelAPI.fetchBookById(id); //     
    console.log(books);
    return books; //   
  } catch (error) {}
});
